/* Add this CSS in a separate style or directly in your component */

.container {
    max-width: 600px;
    margin: auto;
    padding: 20px;
  }
  
  .add-task-container {
    padding: 20px;
    max-width: 650px;
    margin: 20px auto; /* Center the add-task-container */
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(83, 82, 82, 0.1);
  }
  
  .add-task-container input {
    width: 100%;
    padding: 8px;
    margin: 8px 0;
    box-sizing: border-box;
  }
  
  .add-task-container button {
    background-color: #1c88cf;
    color: #fff;
    padding: 10px;
    border: none;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .add-task-container button:hover {
    background-color: #157abd;
  }
  
  /* The following styles are for assigned tasks container */
  .assigned-tasks-container {
    display: grid;
    padding: 20px;
    max-width: 700px;
    margin: 20px auto;
    gap: 20px;
  }
  
  .assigned-tasks-container .row {
    background-color: #fff;
    padding: 15px;
    border-radius: 8px;
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.1);
  }
  
  /* Keep the original colors for edit and delete buttons */
  .assigned-tasks-container button.btn-primary {
    background-color: #3498db;
    transition: background-color 0.3s ease;
  }
  
  .assigned-tasks-container button.btn-danger {
    background-color: #e51616;
    color: #fff;
    transition: background-color 0.3s ease;
  }
  
  /* Add hover effect */
  .assigned-tasks-container button.btn-primary:hover {
    background-color: #2980b9 !important; /* For edit button */
  }
  
  .assigned-tasks-container button.btn-danger:hover {
    background-color: #c70000 !important; /* For delete button */
  }
  
